@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
:root {
  --darkGrey: #303030;
  --grey: #f0f0f0;
  --lightGrey: #f5f5f5;
  --pink: #e800ed;
}
html {
  height: 100%;
}
body {
  min-height: 100%;
  background-color: #f5f5f5;
  background-color: var(--lightGrey);
}

.App {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 6rem 1fr;
}
header {
  width: 100%;
}
main {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 750px;
  grid-column-gap: 4rem;
  -webkit-column-gap: 4rem;
          column-gap: 4rem;
  margin: 2rem 6rem;
}
.mainElement {
  box-shadow: 0px 0px 14px 2px rgba(0, 0, 0, 0.74);
}

.form {
  background-color: white;
  -webkit-align-self: start;
          align-self: start;
}

/*FORM SECTION STYLING***********************************/
.formSectionTitle {
  color: var(--lightGrey);
  background-color: var(--darkGrey);
  padding: 1rem 2rem;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.photoButton {
  display: none;
}
.formSectionTitle > h2 {
  font-weight: 400;
  font-size: 1.2rem;
}
.formSection {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.formSection > span {
  margin: 1.5rem 0;
}

/*FORM SUBSECTION STYLING********************************/
.formSubsection {
  color: var(--darkGrey);
  width: 100%;
  padding: 0 2rem;
  margin: 1.5rem 0;
  display: grid;
  grid-row-gap: 1rem;
  row-gap: 1rem;
}
.formSubsectionTitle {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  border-bottom: var(--darkGrey) solid 1px;
}
.materialButton {
  cursor: pointer;
}
.materialButton:hover {
  color: var(--pink);
}

.inputElement {
  width: 100%;
  color: var(--darkGrey);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.inputElement > input {
  width: 100%;
  border: none;
  padding: 0.2rem;
  margin-top: 0.5rem;
  border-radius: 0.2rem;
  padding: 0.4rem 1rem;
  border: solid var(--darkGrey) 1px;
}
.inputElement > input:focus-visible {
  outline-color: var(--pink);
}

header {
  background-color: var(--darkGrey);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0 6rem;
  color: var(--pink);
}

header > h1 {
  font-size: 2.2rem;
}

button {
  background-color: var(--pink);
  border: none;
  padding: 0.3rem 0;
  font-size: 0.9rem;
  width: 6rem;
  margin-left: 1rem;
  border-radius: 0.2rem;
  cursor: pointer;
}

button:hover {
  box-shadow: 0px 0px 16px 6px rgba(232, 0, 237, 0.4);
  color: white;
}

.buttonContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

/*GENERAL STYLING**********************************************/
.preview {
  background-color: white;
  display: grid;
  grid-template-columns: 35% 65%;
  grid-template-rows: 15rem 1fr;
  color: var(--darkGrey);
  font-size: 0.9rem;
  min-height: 1120px;
  align-self: start;
}
.previewHeader {
  background-color: var(--darkGrey);
  color: var(--lightGrey);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0.5rem 0;
  margin-top: 1.5rem;
}
.previewHeader > h3 {
  font-weight: 400;
  font-size: 1.1rem;
}

/*HEADER STYLING***********************************************/
.headerPreview {
  background-color: var(--darkGrey);
  color: var(--grey);
  grid-column: span 2;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.headerPreview > h2 {
  font-size: 3.5rem;
  font-weight: 600;
  margin: 1rem 0;
}
.headerPreview > h3 {
  font-size: 1.5rem;
  font-weight: 400;
}

/*SIDEBAR STYLING**********************************************/
.sideBarPreview {
  background-color: var(--lightGrey);
  padding: 1.5rem 1rem;
}
.image {
  height: 16rem;
  background-color: var(--darkGrey);
  margin-bottom: 1.5rem;
}
.image > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center center;
}
.sidebarListElement {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 1.3rem;
}
.sidebarListElement > span {
  color: var(--darkGrey);
  margin-right: 0.5rem;
}

/*MAIN STYLING*************************************************/
.mainPreview {
  padding: 0 1rem 1.5rem 1rem;
}
.mainExperienceCard {
  background-color: var(--lightGrey);
  padding: 1rem;
  margin-top: 1rem;
}
.mainExperienceTitle {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-weight: 600;
  margin-bottom: 0.7rem;
}

